___CSS_LOADER_EXPORT___.push([module.id, `.bot.about.header {
    height: 100vh;
    width: 100%;
    background: linear-gradient(180deg, rgba(27,9,56,1) 0%, rgba(27,9,56,1) 10%, rgba(27,9,56,0.8) 100%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
}

.bot.about.site_title {
    height: 8rem;
}
.bot.about.site_subtitle {
    font-size: 2rem;
    text-align: center;
}

.bot.about.feature.section {
    min-height: 32rem;
}

.bot.about.feature.title {
    font-weight: bold;
    font-size: 3rem;
}
.bot.about.feature.description {
    font-size: 1.5rem;
}

.bot.about.footer {
    background-color: rgb(27, 9, 56);
    color: white;
    text-align: center;
    padding: 5rem 1rem;
    min-height: 100vh;
    position: relative;
}
.bot.about.footer_bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100vw;
    color: lightgray;
    padding: 1rem 0.5rem;
}

.bot.about.twitter_link_group {
    margin: 5rem 0;
}
.bot.about.twitter_link {
    text-decoration: none;
    color: white;
    display: block;
    transition: transform ease 0.5s;
}
.bot.about.twitter_link:hover {
    transform: scale(1.2);
}

.bot.about.navi_menu {
    transition: all ease 0.5s;
    position: fixed;
    top: 0;
    left: 0;
}
.bot.about.navi_menu.show {
    background-color: white;
    box-shadow: 0 0 5px lightgray;
}

.bot.about.image_slider {
    position: relative;
    padding-top: 100%;
    overflow: hidden;
}
.bot.about.image_slider img {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    transition: z-index ease 0.5s, opacity ease 0.5s;
    width: 100%;
}
.bot.about.image_slider img.vanish {
    opacity: 0;
}`, "",{"version":3,"sources":["webpack://./src/components/page/about.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,WAAW;IACX,oGAAoG;IACpG,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,YAAY;AAChB;;AAEA;IACI,YAAY;AAChB;AACA;IACI,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;IACjB,eAAe;AACnB;AACA;IACI,iBAAiB;AACrB;;AAEA;IACI,gCAAgC;IAChC,YAAY;IACZ,kBAAkB;IAClB,kBAAkB;IAClB,iBAAiB;IACjB,kBAAkB;AACtB;AACA;IACI,kBAAkB;IAClB,SAAS;IACT,OAAO;IACP,YAAY;IACZ,gBAAgB;IAChB,oBAAoB;AACxB;;AAEA;IACI,cAAc;AAClB;AACA;IACI,qBAAqB;IACrB,YAAY;IACZ,cAAc;IACd,+BAA+B;AACnC;AACA;IACI,qBAAqB;AACzB;;AAEA;IACI,yBAAyB;IACzB,eAAe;IACf,MAAM;IACN,OAAO;AACX;AACA;IACI,uBAAuB;IACvB,6BAA6B;AACjC;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,gBAAgB;AACpB;AACA;IACI,cAAc;IACd,kBAAkB;IAClB,MAAM;IACN,OAAO;IACP,gDAAgD;IAChD,WAAW;AACf;AACA;IACI,UAAU;AACd","sourcesContent":[".bot.about.header {\n    height: 100vh;\n    width: 100%;\n    background: linear-gradient(180deg, rgba(27,9,56,1) 0%, rgba(27,9,56,1) 10%, rgba(27,9,56,0.8) 100%);\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    color: white;\n}\n\n.bot.about.site_title {\n    height: 8rem;\n}\n.bot.about.site_subtitle {\n    font-size: 2rem;\n    text-align: center;\n}\n\n.bot.about.feature.section {\n    min-height: 32rem;\n}\n\n.bot.about.feature.title {\n    font-weight: bold;\n    font-size: 3rem;\n}\n.bot.about.feature.description {\n    font-size: 1.5rem;\n}\n\n.bot.about.footer {\n    background-color: rgb(27, 9, 56);\n    color: white;\n    text-align: center;\n    padding: 5rem 1rem;\n    min-height: 100vh;\n    position: relative;\n}\n.bot.about.footer_bottom {\n    position: absolute;\n    bottom: 0;\n    left: 0;\n    width: 100vw;\n    color: lightgray;\n    padding: 1rem 0.5rem;\n}\n\n.bot.about.twitter_link_group {\n    margin: 5rem 0;\n}\n.bot.about.twitter_link {\n    text-decoration: none;\n    color: white;\n    display: block;\n    transition: transform ea